body {
  font-family: Roboto, sans-serif;
}

.tambola-game {
  margin: auto;
}

.title {
  text-align: center;
}

.btn-generate {
  height: 35px;
  background-color: #32b349e2;
  align-items: center;
  font-weight: bold;
  text-decoration: none;
}

.btn-section {
  gap: 5px;
  display: flex;
}

.hero-section {
  flex-direction: column;
  margin: 15px;
  display: flex;
}

.board-container {
  grid-template-columns: repeat(10, 1fr);
  justify-content: center;
  gap: 5px;
  margin: auto;
  font-size: 25px;
  display: grid;
}

.box {
  border: 1px solid #c3c3c3;
  justify-content: center;
  align-items: center;
  display: inline-grid;
}

.generated {
  color: #4c8aec;
  font-weight: bold;
}

.current {
  color: #fff;
  background-color: #58b0a1;
  font-weight: bold;
}

.play-section {
  justify-content: left;
  align-self: flex-start;
  align-items: flex-start;
  display: none;
}

.show-num {
  color: #209c12;
  visibility: hidden;
  height: 70px;
  justify-content: center;
  margin-top: 10px;
  font-size: 60px;
  display: flex;
}

.sub-title {
  justify-content: center;
  display: flex;
}

.btn-tambola {
  width: -moz-fit-content;
  width: fit-content;
  background-color: #eae2d4;
  border: 1px solid #7e7462;
  border-radius: 4px;
  margin: auto;
  padding: 10px;
}

#resume-restart, #play-btns, #play, #unmute, #pace-section, #manual-reset {
  display: none;
}

#play-btns {
  gap: 20px;
}

.icon {
  color: #36a9ae;
  cursor: pointer;
  font-size: 40px !important;
}

.button {
  height: 40px;
  color: #fff;
  cursor: pointer;
  text-align: center;
  background-color: #36a9ae;
  background-image: linear-gradient(#37adb2, #329ca0);
  border: 1px solid #2a8387;
  border-radius: 4px;
  padding: 11px 15px 12px;
  font-size: 17px;
  line-height: 100%;
  transition: box-shadow 50ms ease-in-out, opacity 50ms ease-in-out;
  box-shadow: 0 1px 1px #0000001f;
}

.button:hover {
  text-decoration: none;
  transition-duration: .15s, .15s;
  box-shadow: inset 0 0 2px #ffffff4d, 0 1px 2px #0006;
}

.button:active {
  box-shadow: inset 0 2px 4px #00000026, 0 1px 1px #0006;
}

.button:disabled {
  cursor: not-allowed;
  opacity: .6;
}

.button:disabled:active {
  pointer-events: none;
}

.button:disabled:hover {
  box-shadow: none;
}

@media (max-width: 500px) {
  .box {
    padding: 5px;
  }

  .board-container {
    gap: 15px 3px;
    margin: auto 20px;
    font-size: 18px;
  }
}

@media (min-width: 500px) {
  .tambola-game {
    max-width: 600px;
  }
}

